<template>
  <Transition name="modal">
    <div class="modal" @click="closeModal">
      <div class="modal__block" @click.stop>
        <h3 class="modal__block_title">
          {{ edit ? $t('changeNote') : $t('addNote') }}
        </h3>

        <div class="modal__block_inputs">
          <label>
            <span>Title</span>
            <input v-model="title" type="text" placeholder="title" />
          </label>
          <label>
            <span>Content</span>
            <textarea v-model="text" placeholder="Content"></textarea>
          </label>
        </div>

        <div class="modal__block_btns">
          <button class="modal__block_btns-btn edit" @click="closeModal">
            {{ $t('cancel') }}
          </button>
          <button
            v-if="!edit"
            class="modal__block_btns-btn del"
            @click="addNote"
          >
            {{ $t('add') }}
          </button>
          <button v-else class="modal__block_btns-btn del" @click="changeNote">
            {{ $t('change') }}
          </button>
        </div>
      </div>
    </div>
  </Transition>
</template>

<script>
import { v4 as uuidv4 } from 'uuid'
export default {
  props: {
    edit: Boolean,
    editedNotes: Object,
  },
  data() {
    return {
      title: '',
      text: '',
    }
  },
  methods: {
    closeModal() {
      this.$emit('close')

      this.title = this.text = ''
    },
    addNote() {
      if (this.title != '' && this.text != '') {
        const note = {
          id: uuidv4(),
          title: this.title,
          text: this.text,
          /* 29.09.23 */
          date: new Date().toLocaleDateString(),
        }
        this.$emit('addNote', note)
        this.closeModal()
      }
    },
    changeNote() {
      const newEditedNote = {
        id: this.editedNotes.id,
        title: this.title,
        text: this.text,
        date: new Date().toLocaleDateString(),
      }
      this.$emit('changeNote', newEditedNote)
      this.closeModal()
    },
  },
}
</script>
