<template>
  <nav class="nav" v-if="nav">
    <button class="nav__lang" @click="switchLang">{{ $i18n.locale }}</button>
    <h1 class="nav__title">{{ $t('notes') }}</h1>

    <button class="nav__search-icon" @click="nav = !nav">
      <img src="@/assets/img/search.svg" alt="" />
    </button>
  </nav>

  <nav class="nav" v-else>
    <button class="nav__back" @click="nav = !nav">
      <img src="@/assets/img/back.svg" alt="" />
    </button>

    <input type="text" :placeholder="$t('searchInput')" v-model="search" autofocus />

    <button class="nav__clear" @click="search = ''">
      <img src="@/assets/img/close.svg" alt="" />
    </button>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      nav: true,
      search: '',
    }
  },
  watch: {
    search(newVal) {
      this.$emit('search', newVal)
    },
  },
  methods: {
    switchLang() {

      this.$i18n.locale == 'ru' ? this.$i18n.locale = 'eng' : this.$i18n.locale = 'ru'

      localStorage.lang = this.$i18n.locale

    }
  }
}
</script>
