<template>
  <div class="notes container">
    <div class="notes__top">
      <h2 class="notes__top_title">{{ notes.length > 0 ? $t('allNotes') : $t('noNotes') }}</h2>

      <button class="notes__top_btn" @click="view = !view">
        <img v-show="view" src="@/assets/img/list.svg" alt="" />
        <img v-show="!view" src="@/assets/img/layout.svg" alt="" />

        <span>{{ view ? $t('list') : $t('grid') }}</span>
      </button>
    </div>

    <div class="notes__list" :class="{ active: !view }">
      <NotesItem v-for="note in notes" :key="note.id" :note="note" :view="view" @delNote="$emit('delNote', note.id)"
        @change="$emit('change', note.id)" />
    </div>
  </div>
</template>

<script>
import NotesItem from '@/components/NotesItem.vue'
export default {
  data() {
    return {
      view: true,
    }
  },
  components: {
    NotesItem,
  },
  props: {
    notes: {
      typeof: Array,
    },
  },
}
</script>
