<template>
  <Navbar @search="searchValue = $event" />
  <Notes :notes="filterNotes" @delNote="delNote" @change="change" />

  <Modal
    v-show="modalOpen"
    @close="modalOpen = false"
    @addNote="addNote"
    :edit="edit"
    :editedNotes="editedNotes"
    @changeNote="changeNote"
  />

  <button
    class="addNote"
    @click=";(modalOpen = !modalOpen), (edit = false)"
    v-show="!modalOpen"
  >
    <img src="@/assets/img/pens.svg" alt="" />
  </button>
</template>

<script>
import Navbar from '@/components/Navbar.vue'
import Modal from './components/Modal.vue'
import Notes from './components/Notes.vue'

export default {
  components: {
    Navbar,
    Notes,
    Modal,
  },
  data() {
    return {
      notes: [],
      modalOpen: false,
      edit: false,
      editedNotes: null,
      searchValue: null,
    }
  },
  methods: {
    addNote(note) {
      this.notes.push(note)
    },
    delNote(id) {
      let index = this.notes.findIndex((note) => note.id == id)

      this.notes.splice(index, 1)
    },

    getNotes() {
      let localNotes = localStorage.notes
      if (localNotes) {
        this.notes = JSON.parse(localNotes)
      }
    },

    change(id) {
      this.modalOpen = this.edit = true

      this.editedNotes = this.notes.find((note) => note.id == id)
    },
    changeNote(note) {
      this.notes.forEach((item) => {
        if (item.id == note.id) {
          ;(item.title = note.title),
            (item.text = note.text),
            (item.date = note.date)
        }
      })
    },
  },
  computed: {
    filterNotes() {
      return this.searchValue
        ? this.notes.filter((note) =>
            note.title.toLowerCase().includes(this.searchValue.toLowerCase())
          )
        : this.notes
    },
  },

  watch: {
    // text(newVal) {
    //   console.log(`Новое значение в инпуте ${newVal}`)
    // },
    notes: {
      handler() {
        localStorage.notes = JSON.stringify(this.notes)
      },
      deep: true,
    },
  },

  created() {
    this.getNotes()
  },
}
</script>
